.maincontent {
  margin-top: 100px;
  margin-left: 150px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
}
.infocontainer {
  margin: 5rem 2rem 0 0;
}
.header {
  color: white;
}
.line {
  /* position: absolute; */
  /* z-index: -1; */
  background: #1bf8f8;
  height: 0.2rem;
  width: 14rem;
  /* left: 26rem; */
  /* top: 1rem; */
}

.listContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0;
  list-style: none;
}
.ul {
  padding-left: 0;
}

.list:before {
  border-style: solid;
  border-width: 0.1em 0.1em 0em 0em;
  border-color: #1bf8f8;
  content: "";
  display: inline-block;
  height: 0.45em;
  left: 0.15em;
  position: relative;
  top: 0.3em;
  transform: rotate(-45deg);
  vertical-align: top;
  width: 0.45em;
  left: 0;
  transform: rotate(45deg);
  margin-right: 20px;
  z-index: -1;
}
@media (max-width: 780px) {
  .listContainer {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0;
    list-style: none;
  }
  .line {
    display: none;
  }
  .infocontainer {
    max-width: 100%;
  }
  .maincontent {
    margin-top: auto;
    margin-left: 10%;
    display: flex;
  }
}
