.maincontent {
  height: 100vh;
  margin-top: 100px;
}
.infocontainer {
  display: flex;
  flex-direction: column;
  font-size: 1.8rem;
  height: 60%;
  text-align: center;
  justify-content: center;
}

.name {
  font-size: 2.5rem;
  color: #1bf8f8;
}

.iconsContainer {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  width: 130px;
  font-size: 1.4rem;
}
.icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: -100px;
  margin-right: 90px;
  animation: icons-move 3s ;
}
.icons:hover {
  margin-left: 10px;
  margin-right: 0;
  transition-duration: 600ms;
  color: #ffffff;
}
@media (max-width: 750px) {
  .icons {
    margin-left: 10px;
    margin-right: -10px;
  }
}
@keyframes icons-move {
  0%{
    transform: translateX(500%);
    /* transform: translateY(50%); */
  }
  100%{
    transform: translateX(0%);
    /* transform: translateY(100%); */
  }
}
